import React from 'react';
import devsinc_logo from '../Assets/Devsinc_logo.png'; //Company Logo
import './LeaveRequest.css'; //For styling


// This is the LeaveRequest component responsible for rendering the leave request form
function LeaveRequest() {
    return (
        <>
            {/* Main container for the leave request form */}
            <div className ='bodyLeave'>
                {/* Company logo section */}
                <div className="realLogo-container">
                    <img src={devsinc_logo} alt="Company Logo" className="realLogo" />
                </div>

                {/* Banner section */}
                <div className="leave-request-banner">
                    <h1>Leave Request</h1>
                </div>

                {/* Center wrapper for the form */}
                <div className="center-wrapper">
                    {/* Content container */}
                    <div className="content-container">
                        {/* Leave request form */}
                        <form id="leaveRequestForm">
                            {/* Leave type selection */}
                            <div className="form-group">
                                <label htmlFor="leaveType">Leave Type</label>
                                <select id="leaveType" name="leaveType">
                                    <option value="sick">Sick Leave</option>
                                    <option value="annual">Annual Leave</option>
                                    <option value="other">Other</option>
                                </select>
                            </div>

                            {/* Date selection */}
                            <div className="form-group">
                                <label htmlFor="date">Date</label>
                                <input type="date" id="date" name="date" />
                            </div>

                            {/* About textarea */}
                            <div className="form-group">
                                <label htmlFor="about">About</label>
                                <textarea id="about" name="about" rows="4" placeholder="About..."></textarea>
                            </div>

                            {/* Attachments file input */}
                            <div className="form-group">
                                <label htmlFor="attachments">Attachments</label>
                                <input type="file" id="attachments" name="attachments" multiple />
                            </div>

                            {/* Form actions buttons */}
                            <div className="form-actions">
                                <button type="button" id="cancel">Cancel</button>
                                <button type="submit" id="request">Request</button>
                            </div>
                        </form>
                    </div>
                </div>

                {/* Site footer (currently commented out) */}
                {/* <footer className="site-footer">
                </footer> */}
            </div>
        </>
    );
}

export default LeaveRequest; // Exporting the LeaveRequest component as default
